body{
  margin: 0;
  padding: 0;
  background-color: grey;
}

*{
  box-sizing: border-box;
}
.container{
  padding: 10px;
  text-align: center;
  color:black;

}
.title{
  font-size: 3rem;
  font-family: 'Sriracha', cursive;
}
@media(max-width:500px){
  body{
    background: url("./assets/image1.jpg");
    background-size: 500px 800px;
    background-repeat: repeat;
  }
  .container .title{
    font-size: 3rem;
    color: white;
  }
  .container .form{
    display: flex;
    flex-direction: column;
  }
  .container .label{
    display: none;
  }
  .container .input{
    width: 100%;
    margin: 0;
  }
  .container .button{
    margin-left: 21%;
    margin-top: 5%;
    width: 200px;
  }
  .container .image{
    width:300px;
    margin-top: 10px;
  }
}
.form{
  display: grid;
  grid-template-columns:auto 1fr auto ;

}
.label{
  font-size: 2rem;
}
.input{
  height: 50px;
  border: 1px solid #ddd;
  border-radius:20px ;
  line-height: 2.8rem;
  font-size: 1.2rem;
  margin: 0 2rem;
}
input:focus{
  outline: none;
}
.button{
  width: 300px;
  border: 1px solid rgba(0, 0,0,0.7);
  background-color: rgba(0, 0,0,0.7);
  color: white;
  font-size: 2rem;
  border-radius: 20px;
}
.button:focus{
  outline: none;
}
.button:hover{
background-color: rgba(0, 0, 0, 0.5);
}
.cards{
margin: 50px;
width: 500px;
background-color: white;
}
.cards:hover{
 box-shadow: 10px 10px 10px 10px rgb(177, 175, 175);
}
.cards_main{
  display: grid;
  grid-template-columns: auto auto auto;
}
@media(max-width: 1505px){
  .container .cards_main{
    grid-template-columns: auto auto;
  }
}
@media(max-width:500px){
  .container .cards_main{
    display: flex;
    flex-direction: column;
    margin: 0;
  }
  .container .cards{
  margin: 0;
  margin-top: 20px;
  width: 100%;
  }
}